.container {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: $mobile) {
        width: 480px;
    }
    @media screen and (min-width: $tablet) {
        width: 768px;
        padding-left: 34px;
        padding-right: 34px;
    }
    @media screen and (min-width: $desktop) {
        width: 1280px;
        padding-left: 115px;
        padding-right: 115px;
    }
}